import React, { useEffect, useRef, useState, VFC } from 'react';
import Webcam from 'react-webcam';
import { css } from '@emotion/css';
import { Camera } from '@mediapipe/camera_utils';
import { Hands, Results, NormalizedLandmarkList } from '@mediapipe/hands';
import { drawCanvas } from '../utils/drawCanvas'; // Import your drawCanvas function

export const App: VFC = () => {
    const webcamRef = useRef<Webcam>(null)
    const canvasRef = useRef<HTMLCanvasElement>(null)
    const [showPopup, setShowPopup] = useState(false);

    useEffect(() => {
        const hands = new Hands({
            locateFile: (file) => {
                return `https://cdn.jsdelivr.net/npm/@mediapipe/hands/${file}`;
            },
        });

        hands.setOptions({
            maxNumHands: 2,
            modelComplexity: 1,
            minDetectionConfidence: 0.5,
            minTrackingConfidence: 0.5,
        });

        hands.onResults((results: Results) => {
            const canvasCtx = canvasRef.current!.getContext('2d')!;
            drawCanvas(canvasCtx, results);

            if (results.multiHandLandmarks && results.multiHandLandmarks.length > 0) {
                const isOkGestureDetected = detectOkGesture(results.multiHandLandmarks);
                setShowPopup(isOkGestureDetected);
            } else {
                setShowPopup(false);
            }
        });

        if (typeof webcamRef.current !== 'undefined' && webcamRef.current !== null) {
            const camera = new Camera(webcamRef.current.video!, {
                onFrame: async () => {
                    await hands.send({ image: webcamRef.current!.video! });
                },
                width: 1280,
                height: 720,
            });
            camera.start();
        }
    }, []);

	const detectOkGesture = (handLandmarks: NormalizedLandmarkList[]): boolean => {
		if (handLandmarks.length === 0) {
		  return false;
		}
	  
		const thumbTip = handLandmarks[0][4];
		const indexFingerTip = handLandmarks[0][8];
	  
		const thumbX = thumbTip.x;
		const thumbY = thumbTip.y;
		const indexX = indexFingerTip.x;
		const indexY = indexFingerTip.y;
	  
		// Calculate distance between thumb and index fingertip
		const distance = Math.sqrt(Math.pow(thumbX - indexX, 2) + Math.pow(thumbY - indexY, 2));
	  
		// Check if distance is close to zero (pinch gesture)
		const threshold = 0.05; // Adjust threshold as needed
		if (distance < threshold) {
		  return true;
		}
	  
		return false;
	  };
    return (
        <div className={styles.container}>
            <Webcam
                audio={false}
                style={{ visibility: 'hidden' }}
                width={1280}
                height={720}
                ref={webcamRef}
                screenshotFormat="image/jpeg"
                videoConstraints={{ width: 1280, height: 720, facingMode: 'user' }}
            />
            <canvas ref={canvasRef} className={styles.canvas} width={1280} height={720} />
            {showPopup && (
                <div className={styles.popup}>
                    <div className={styles.popupContent}>OK Gesture Detected!</div>
                </div>
            )}
        </div>
    );
};

// CSS styles
const styles = {
    container: css`
        position: relative;
        width: 100vw;
        height: 100vh;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
    `,
    canvas: css`
        position: absolute;
        width: 1280px;
        height: 720px;
        background-color: #fff;
    `,
    popup: css`
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #00ff00; /* Green */
        color: #fff;
        padding: 10px;
        border-radius: 5px;
    `,
    popupContent: css`
        text-align: center;
    `,
};
